import React, {memo, useCallback, useState} from 'react';
import {SingleTabView} from '@true/ui';
import {EventThumbnail} from '@/screens/EventScreen/EventThumbnail';
import {getEventsByQuery, useEventsByQuery} from '@/store/events';
import {
  requestEventsChannel,
  requestEventsJoinedChannel,
} from '@/store/events/function';
import {HeaderTabCustom} from '@/screens/DetailChannelScreen/HeaderTabChannel';
import {useTheme} from '@true/core';
import {IFilterParams} from '@/screens/SearchScreen/type';

interface EventChannelProps {
  index: number;
  path: string;
}

export const EventChannel = memo(function (props: EventChannelProps) {
  const {index, path} = props;
  const theme = useTheme();
  const [params, setParams] = useState<IFilterParams>({});
  const [value, setValue] = useState('');

  const onChangeText = useCallback(
    async (q: string) => {
      setValue(q);
    },
    [params],
  );
  const renderItem = useCallback(
    (item: string | number, index?: number, isLast?: boolean) => {
      return <EventThumbnail id={item as number} />;
    },
    [path],
  );
  return (
    <SingleTabView.Provider
      index={index}
      // level={1}
      renderItem={renderItem}
      getItemByQuery={getEventsByQuery}
      useItemByQuery={useEventsByQuery}
      itemHeight={300}
      tabs={[
        {
          title: 'Public event',
          content: p => (
            <SingleTabView.List
              singleListIndex={p.singleListIndex}
              params={{path, q: value}}
              requestFn={requestEventsChannel}
              query={path}
            />
          ),
        },
        {
          title: 'Joined',
          content: p => (
            <SingleTabView.List
              singleListIndex={p.singleListIndex}
              requestFn={requestEventsJoinedChannel}
              params={{path, q: value}}
              query={`joined_${path}`}
            />
          ),
        },
      ]}>
      <SingleTabView.CollapsableTabBar
        hideSeparator={true}
        >
        <HeaderTabCustom title={'Events'} onChangeText={onChangeText} />
      </SingleTabView.CollapsableTabBar>
      <SingleTabView.Content />
    </SingleTabView.Provider>
  );
});
