import React, {memo, useMemo} from 'react';
import {View} from 'react-native';
import {ScrollableList, SingleTabView, UIButton, UIText} from '@true/ui';
import {EmptyView} from '@/components/EmptyView';
import {EUserRole, FlexCenter, FlexSpaceBetween} from '@/utils/helper';
import {IC_OUTLINED_SEARCH} from '@true/asset';
import {styled, useMe, useTheme, useUserByQuery} from '@true/core';
import {ETypeChannel, IChannel} from '@/store/channels/type';
import {MemberItem} from '@/screens/DetailChannelScreen/MemberItem';
import {ActionMemberChannelBottomSheet} from '@/screens/DetailChannelScreen/BottomSheet/ActionMemberChannel';

interface MemberChannelProps {
  data: IChannel | undefined;
  index: number;
}

export const MemberChannel = memo(function MemberChannel(
  props: MemberChannelProps,
) {
  const {data, index} = props;
  const me = useMe();
  const listMembersCompany = useUserByQuery('allIds');
  const listMembers = useMemo(() => {
    if (data?.type === ETypeChannel.COMPANY) {
      return listMembersCompany;
    } else return data?.followers;
  }, []);
  const isAdmin = useMemo(() => {
    return (
      data?.owners?.includes(Number(me?.id) || 0) ||
      Number(me?.role) === EUserRole.ADMIN
    );
  }, []);
  return (
    <SingleTabView.Provider
      index={index}
      tabs={[
        {
          title: 'Members',
          content: p => (
            <ScrollableList
              data={listMembers || []}
              ListEmptyComponent={<EmptyView text="No member was found" />}
              loading={false}
              renderItem={({item}: any) => {
                return <MemberItem id={item} isAdmin={isAdmin} />;
              }}
              singleListIndex={p.singleListIndex}
              index={index}
            />
          ),
        },
        {
          title: 'Admin',
          content: p => (
            <ScrollableList
              data={data?.owners || []}
              ListEmptyComponent={<EmptyView />}
              loading={false}
              renderItem={({item}: any) => {
                return <MemberItem id={item} isAdmin={isAdmin} />;
              }}
              singleListIndex={p.singleListIndex}
              index={index}
            />
          ),
        },
        {
          title: 'Invitations',
          content: p => (
            <ScrollableList
              data={[]}
              ListEmptyComponent={<EmptyView text="No invitations was found" />}
              loading={false}
              renderItem={({item}: any) => {
                return <MemberItem id={item} isAdmin={isAdmin} />;
              }}
              singleListIndex={p.singleListIndex}
              index={index}
            />
          ),
        },
      ]}>
      <View
        style={{
          flex: 1,
          backgroundColor: 'white',
        }}>
        <SingleTabView.Content />
      </View>
      <SingleTabView.CollapsableTabBar hideSeparator={true}>
        <SContainer>
          <FlexSpaceBetween>
            <UIText.HeaderMedium>Members</UIText.HeaderMedium>
            <FlexCenter>
              {/*<UIButton source={IC_OUTLINED_PERSON_ADD}/>*/}
              <UIButton source={IC_OUTLINED_SEARCH} />
            </FlexCenter>
          </FlexSpaceBetween>
        </SContainer>
      </SingleTabView.CollapsableTabBar>
    </SingleTabView.Provider>
  );
});
const SContainer = styled.View`
  padding: 8px 16px;
  background-color: white;
`;
