import {Fonts, styled} from '@true/core';
import React, {memo} from 'react';
import {Image, View} from 'react-native';
import {IC_OUTLINED_SEARCH} from '@true/asset';
import {MySearchBar} from '@/screens/SearchScreen/SearchBar';
import Animated, {
  useAnimatedStyle,
  useSharedValue,
  useWorkletCallback,
  withTiming,
} from 'react-native-reanimated';
import Metrics from '@/assets/Metrics';
import {UIButton} from '@true/ui';

interface HeaderTabCustomProps {
  title: string;
  onChangeText?: (q: string) => void;
}

export const HeaderTabCustom = memo(function (props: HeaderTabCustomProps) {
  const {title, onChangeText} = props;
  const isShow = useSharedValue(false);
  const animatedStyle = useAnimatedStyle(() => ({
    opacity: isShow.value ? 0 : 1,
  }));
  const animatedSearchStyle = useAnimatedStyle(() => ({
    //44= 16 +16 +12 + 16
    width: withTiming(isShow.value ? Metrics.screen.width - 60 : 0),
    opacity: withTiming(isShow.value ? 1 : 0),
    display: isShow.value ? 'flex' : 'none',
  }));
  const toggleSearchBox = useWorkletCallback(() => {
    isShow.value = !isShow.value;
  }, []);
  return (
    <View
      style={{
        backgroundColor: 'white',
      }}>
      <SSearchView style={animatedSearchStyle}>
        <MySearchBar
          containerStyle={{
            backgroundColor: 'white',
            width: '100%',
          }}
          onSearchTextChange={onChangeText}
        />
        <SCancelButton onPress={toggleSearchBox} />
      </SSearchView>
      <SContainer pointerEvents={'box-none'} style={animatedStyle}>
        <STitle>{title}</STitle>
        <SIcon onPress={toggleSearchBox}>
          <Image source={IC_OUTLINED_SEARCH} />
        </SIcon>
      </SContainer>
    </View>
  );
});
const SContainer = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
  background-color: white;
`;

const STitle = styled.Text`
  font-family: ${Fonts.Bold};
  font-size: 16px;
  line-height: 24px;
  color: ${p => p.theme.neutral1};
`;
const SIcon = styled.TouchableOpacity`
  width: 48px;
  height: 48px;
  align-items: center;
  justify-content: center;
`;
const SSearchView = styled(Animated.View)`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 0 16px;
  border-radius: 8px;
  flex-direction: row;
  //align-items: center;
  background-color: white;
  justify-content: flex-end;
  height: 80%;
  z-index: 10;
  margin-top: 4px;
`;
const SCancelButton = styled(UIButton).attrs(p => ({
  title: 'Cancel',
  textStyle: {
    color: p.theme.neutral2,
  },
}))`
  align-items: center;
  justify-content: center;
  margin-left: 12px;
`;
