import {Fonts, styled} from '@true/core';
import {memo, useCallback} from 'react';
import {
  IC_FILLED_GROUP,
  IC_FILLED_PERSON_ADD,
  IC_OUTLINED_PUBLIC,
} from '@true/asset';
import {FlexSpaceBetween} from '@/utils/helper';
import {AttendeeAvatarList} from '@/screens/EventScreen/AttendeeAvatarList';
import {globalStyles} from '@/utils/globalStyle';
import {navigateToAddMemberScreen} from '@/utils';
import {requestInviteEvent} from '@/store/events/function';
import {requestAddMemberChannel} from '@/store/channels/function';
import {IMG_DEFAULT_CHANNEL} from '@true/asset';

interface CoverDetailChannelProps {
  image_uri?: string;
  title?: string;
  list_members?: number[];
  type?: string;
  channelPath?: string;
  channel_id?: string;
  followers?: number[];
}

export const CoverDetailChannel = memo(function (
  props: CoverDetailChannelProps,
) {
  const {
    image_uri,
    title,
    list_members,
    type,
    channelPath,
    channel_id,
    followers,
  } = props;

  const onAddMemberChannel = useCallback(
    async (ids: number[], usernameIds: string) => {
      await requestAddMemberChannel({
        id: channel_id || '',
        users: usernameIds,
      });
    },
    [],
  );
  const onAddMember = useCallback(() => {
    navigateToAddMemberScreen({
      ids: followers || [],
      onSubmit: onAddMemberChannel,
    });
  }, [followers]);
  return (
    <>
      <SCoverImage
        source={image_uri ? {uri: image_uri} : IMG_DEFAULT_CHANNEL}
      />
      <SInfoWrapper>
        <STitle>{title}</STitle>
        <SRow>
          <SIcon>
            <SImageIcon source={IC_OUTLINED_PUBLIC} />
          </SIcon>
          <STextNormal>
            {type === 'company' ? 'Company-wide' : 'Group'}
          </STextNormal>
          <STextNormal>{`  |  `}</STextNormal>
          <STextTag>{`@${channelPath}`}</STextTag>
        </SRow>
        <SRow>
          <SIcon>
            <SImageIcon source={IC_FILLED_GROUP} />
          </SIcon>
          <STextNormal>{`${list_members?.length} members`}</STextNormal>
        </SRow>
        <FlexSpaceBetween
          // @ts-ignore
          style={[globalStyles.mt12, globalStyles.ml8]}>
          <AttendeeAvatarList size={32} ids={list_members} maxUsers={6} />
          <SButton onPress={onAddMember}>
            <SInviteIcon source={IC_FILLED_PERSON_ADD} />
          </SButton>
        </FlexSpaceBetween>
      </SInfoWrapper>
    </>
  );
});
const SCoverImage = styled.ImageBackground`
  flex: 1;
  height: 180px;
  width: auto;
  background-color: #0b69a1;
`;
const STitle = styled.Text`
  font-family: ${Fonts.Regular};
  color: ${p => p.theme.neutral1};
  /* Heading/H2 */
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
`;
const SInfoWrapper = styled.View`
  background-color: white;
  padding: 16px;
`;
const SRow = styled.View`
  flex-direction: row;
  align-items: center;
  margin-top: 8px;
`;
const SIcon = styled.View`
  width: 20px;
  height: 20px;
  align-items: center;
  justify-content: center;
  margin-right: 6px;
`;
const STextNormal = styled.Text`
  font-family: ${Fonts.Regular};
  color: ${p => p.theme.neutral2};
  font-size: 14px;
  line-height: 20px;
`;
const SButton = styled.TouchableOpacity`
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 16px;
  background-color: ${p => p.theme.primaryColor};
`;
const SInviteIcon = styled.Image`
  width: 20px;
  height: 20px;
  tint-color: #ffffff;
`;
const SImageIcon = styled.Image`
  width: 20px;
  height: 20px;
  tint-color: ${p => p.theme.neutral2};
`;
const STextTag = styled.Text`
  color: #267cde;
  font-family: ${Fonts.Regular};
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
`;
