import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { goBack, memoForwardRef, styled } from "@true/core";
import { Image } from "react-native";
import { IC_OUTLINED_LEFT, IC_OUTLINED_MORE_HORIZ, IC_OUTLINED_EDIT_SQUARE } from "@true/asset";
import { FlexCenter } from "@/utils/helper";
import { globalStyles } from "@/utils/globalStyle";
import { useCallback } from "react";
import { openChannelActionScreen, openSelectTypePostScreen } from "@/utils";

interface HeaderDetailChannelProps {
  isCompany: boolean;
}
export const HeaderDetailChannel = memoForwardRef(function(props: HeaderDetailChannelProps) {
  const { aTop } = useAnimatedSafeAreaInsets();
  const animatedPaddingTop = useAnimatedStyle(() => ({
    paddingTop: aTop.value
  }));
  const onOpenChannelAction = useCallback(() => {
    openChannelActionScreen({
      isCompany: props.isCompany
    });
  }, []);
  return (
    <SContainer style={animatedPaddingTop}>
      <SIconBack onPress={goBack}>
        <Image source={IC_OUTLINED_LEFT} />
      </SIconBack>
      <FlexCenter>
        <SWritePost onPress={openSelectTypePostScreen} style={globalStyles.mr8}>
          <SImage source={IC_OUTLINED_EDIT_SQUARE} />
        </SWritePost>
        <SIconOption onPress={onOpenChannelAction}>
          <Image source={IC_OUTLINED_MORE_HORIZ} />
        </SIconOption>
      </FlexCenter>
    </SContainer>
  );
});
const SContainer = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: white;
  padding: 8px 16px;
`;
const SIconBack = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {
    top: 8,
    left: 8,
    right: 8,
    bottom: 8
  }
}))`
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
`;
const SImage = styled.Image`
  width: 24px;
  height: 24px
`;
const SIconOption = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {
    top: 8,
    left: 8
  }
}))`
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  margin-right: 4px;
`;
const SWritePost = styled.TouchableOpacity`
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
`;
