import React, {memo, useCallback, useRef, useState} from 'react';
import {ScrollableTabView, UIButton, UIText} from '@true/ui';
import {getUpdatesByQuery, useUpdatesByQuery} from '@/store/updates';
import {PinnedUpdates} from '@/screens/HomeScreen/PinnedUpdates';
import {PostThumbnail} from '@/components/PostThumbnail';
import {
  requestUpdatePosts,
  requestUpdatePostsChannel,
} from '@/store/updates/function';
import {styled, useAsyncFn} from '@true/core';
import {Fonts} from '@true/core';
import {IC_OUTLINED_EXPAND_MORE} from '@true/asset';
import {EOrderUpdate} from '@/store/updates/type';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import OrderUpdateBottomSheet from '@/components/BottomSheets/OrderUpdate';
import {TouchableOpacity} from 'react-native';
import {FlexCenter} from '@/utils/helper';

interface HomeChannelProps {
  index: number;
  path: string | undefined;
  mounted: boolean;
}

export const HomeChannel = memo(function (props: HomeChannelProps) {
  const {index, path, mounted} = props;
  const orderUpdateRef = useRef<BottomSheetModal>(null);
  const listPinnedUpdatesChannel = useUpdatesByQuery(`pinned_${path}`);
  const [order, setOrder] = useState(EOrderUpdate.UPDATE);

  const [{loading}, refreshUpdateChannel] = useAsyncFn(async () => {
    requestUpdatePostsChannel({path: path || '', order}).then();
  }, []);

  const renderItem = useCallback(
    (item: any, index?: number, isLast?: boolean) => {
      return <PostThumbnail isInFeed={true} id={item} key={index} />;
    },
    [],
  );
  // useEffect(() => {
  //   requestUpdatePostsChannel({path:path || ''}).then();
  // }, []);
  return (
    <>
      <ScrollableTabView.List
        index={index}
        requestFn={requestUpdatePostsChannel}
        query={path}
        renderItem={renderItem}
        getItemByQuery={getUpdatesByQuery}
        useItemByQuery={useUpdatesByQuery}
        itemHeight={500}
        params={{path, order}}
        ListHeaderComponent={
          <>
            {listPinnedUpdatesChannel.length > 0 ? (
              <PinnedUpdates listIds={listPinnedUpdatesChannel} />
            ) : null}
            <SContentContainer>
              <TouchableOpacity
                onPress={() => {
                  orderUpdateRef.current?.present();
                }}>
                <FlexCenter>
                  <UIText.HeaderMedium>
                    {order === EOrderUpdate.UPDATE
                      ? 'Recently updated'
                      : 'Recently created'}
                  </UIText.HeaderMedium>
                  <UIButton source={IC_OUTLINED_EXPAND_MORE} />
                </FlexCenter>
              </TouchableOpacity>
            </SContentContainer>
          </>
        }
      />
      <OrderUpdateBottomSheet
        ref={orderUpdateRef}
        setOrder={setOrder}
        refreshHome={refreshUpdateChannel}
      />
    </>
  );
});

const SContentContainer = styled.View`
  flex-direction: row;
  align-items: center;
  padding: 12px 16px;
`;
