import React, {memo, useCallback, useState} from 'react';
import {SingleTabView} from '@true/ui';
import {AnnouncementThumbnail} from '@/screens/AnnouncementScreen/AnnouncementThumbnail';
import {
  getAnnouncementsByQuery,
  useAnnouncementsByQuery,
} from '@/store/announcements';
import {
  requestAnnouncementsChannel,
  requestCreatedAnnouncementsChannel,
} from '@/store/announcements/function';
import {HeaderTabCustom} from '@/screens/DetailChannelScreen/HeaderTabChannel';
import {useTheme} from '@true/core';
import {IFilterParams} from '@/screens/SearchScreen/type';

interface AnnouncementChannelProps {
  index: number;
  path: string;
}

export const AnnouncementChannel = memo(function (
  props: AnnouncementChannelProps,
) {
  const {index, path} = props;
  const theme = useTheme();
  const [params, setParams] = useState<IFilterParams>({});
  const [value, setValue] = useState('');

  const onChangeText = useCallback(
    async (q: string) => {
      setValue(q);
    },
    [params],
  );
  const renderItem = useCallback(
    (item: string | number, index?: number, isLast?: boolean) => {
      return <AnnouncementThumbnail id={item as number} />;
    },
    [path],
  );

  return (
    <SingleTabView.Provider
      index={index}
      // level={1}
      renderItem={renderItem}
      getItemByQuery={getAnnouncementsByQuery}
      useItemByQuery={useAnnouncementsByQuery}
      itemHeight={150}
      tabs={[
        {
          title: 'Announcements',
          content: p => (
            <SingleTabView.List
              // mounted={p.mounted}
              singleListIndex={p.singleListIndex}
              params={{path, q: value}}
              requestFn={requestAnnouncementsChannel}
              query={path}
            />
          ),
        },
        {
          title: 'Created by me',
          content: p => (
            <SingleTabView.List
              singleListIndex={p.singleListIndex}
              params={{path}}
              requestFn={requestCreatedAnnouncementsChannel}
              query={`created_${path}`}
            />
          ),
        },
      ]}>
      <SingleTabView.CollapsableTabBar>
        <HeaderTabCustom title={'Announcement'} onChangeText={onChangeText} />
      </SingleTabView.CollapsableTabBar>
      <SingleTabView.Content />
    </SingleTabView.Provider>
  );
});
