import {getUser, styled, useUser} from '@true/core';
import {Avatar, UIButton, UIText} from '@true/ui';
import React, {memo, useCallback} from 'react';
import {Pressable, View} from 'react-native';
import {IC_OUTLINED_MORE_HORIZ} from '@true/asset';
import {ActionMemberChannelBottomSheet} from '@/screens/DetailChannelScreen/BottomSheet/ActionMemberChannel';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {navigateToProfileScreen} from '@/utils';

interface MemberItemProps {
  id: number;
  isAdmin: boolean;
}

export const MemberItem = memo(function (props: MemberItemProps) {
  const {id, isAdmin} = props;
  const user = useUser(id);
  const bottomSheetRef = React.useRef<BottomSheetModal>(null);
  const onOpenAction = useCallback(() => {
    bottomSheetRef.current?.present();
  }, []);
  const onPress = useCallback(() => {
    if (isAdmin) return;
    else {
      navigateToProfileScreen({
        username: getUser(user?.id || 0)?.username || '',
        user_id: user?.id,
      });
    }
  }, [isAdmin]);
  return (
    <SContainer>
      <Avatar userKey={user?.id || ''} size={40} />
      <SInfoWrapper onPress={onPress}>
        <View>
          <UIText.BodyLarge400>{user?.name}</UIText.BodyLarge400>
          <STextJob>{user?.title || 'No job description'}</STextJob>
        </View>
        {isAdmin ? (
          <UIButton onPress={onOpenAction} source={IC_OUTLINED_MORE_HORIZ} />
        ) : null}
      </SInfoWrapper>
      <ActionMemberChannelBottomSheet id={id} ref={bottomSheetRef} />
    </SContainer>
  );
});
const SContainer = styled.View`
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  padding: 8px 16px;
  background-color: ${p => p.theme.backgroundColor};
  margin-bottom: 4px;
`;

const SInfoWrapper = styled(Pressable)`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex: auto;
  border-bottom-width: 1px;
  border-bottom-color: #ebebeb;
  margin-left: 12px;
  padding-bottom: 16px;
`;
const STextJob = styled(UIText.BodyMedium400)`
  color: ${p => p.theme.neutral2};
`;
