import {ScreenWrapper, ScrollableTabView} from '@true/ui';
import React, {memo, useEffect} from 'react';
import {HeaderDetailChannel} from '@/screens/DetailChannelScreen/HeaderDetailChannel';
import {CoverDetailChannel} from '@/screens/DetailChannelScreen/CoverDetailChannel';
import {EmptyView} from '@/components/EmptyView';
import {AnnouncementChannel} from '@/screens/DetailChannelScreen/AnnouncementChannel';
import {getUserByQuery, useAsyncFn, useNavigationParams} from '@true/core';
import {useChannels} from '@/store/channels';
import {EventChannel} from '@/screens/DetailChannelScreen/EventChannel';
import {HomeChannel} from '@/screens/DetailChannelScreen/HomeChannel';
import {View} from 'react-native';
import {requestChannelPath} from '@/store/channels/function';
import {IMAGE_EMPTY_SEARCH} from '@true/asset';
import {MemberChannel} from '@/screens/DetailChannelScreen/MemberChannel';

export interface DetailChannelScreenParams {
  id: number;
  path: string;
}

export const DetailChannelScreen = memo(function () {
  const {id, path} = useNavigationParams<DetailChannelScreenParams>();
  const channel = useChannels(id);
  const companymembers = getUserByQuery('allIds');

  const [{loading: firstLoading, error: firstError}, loadFirst] = useAsyncFn(
    () => requestChannelPath({path: path}),
    [id],
  );
  const [{loading: refreshing, error: refreshError}, refresh] = useAsyncFn(
    () => requestChannelPath({path: path}),
    [id],
  );

  useEffect(() => {
    loadFirst().then();
  }, []);

  return (
    <ScreenWrapper isGrey={true}>
      <HeaderDetailChannel isCompany={channel?.metatype === 'company'} />
      <ScrollableTabView.Provider
        tabs={[
          {
            title: 'Home',
            content: p => (
              <HomeChannel
                mounted={p.mounted}
                index={p.index}
                path={channel?.path}
              />
            ),
          },
          {
            title: 'Announcements',
            content: p => (
              <AnnouncementChannel index={p.index} path={channel?.path || ''} />
            ),
          },
          {
            title: 'Events',
            content: p => (
              <EventChannel index={p.index} path={channel?.path || ''} />
            ),
          },
          {
            title: 'Members',
            content: p => <MemberChannel index={p.index} data={channel} />,
          },
        ]}>
        <ScrollableTabView.CollapsableTabBar>
          <CoverDetailChannel
            title={channel?.name}
            // @ts-ignore
            list_members={
              channel?.metatype === 'company'
                ? companymembers
                : channel?.followers
            }
            image_uri={channel?.image}
            type={channel?.metatype}
            channelPath={channel?.path}
            followers={channel?.followers}
          />
        </ScrollableTabView.CollapsableTabBar>
        <View style={{flex: 1, marginTop: 8}}>
          <ScrollableTabView.Content />
        </View>
      </ScrollableTabView.Provider>

    </ScreenWrapper>
  );
});
export default DetailChannelScreen;
